import React, {useState} from "react";
import {useForm} from "react-hook-form";
import {CCard, CCardHeader, CCardBody} from "@coreui/react";
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../../../utils/Common";
import axios from "axios";
import {ErrorMessage} from "@hookform/error-message"

const CreateDistributor = () => {
  const {
    register,
    handleSubmit,
    formState: {errors},
    reset
  } = useForm();

  const [btnDisable, setBtnDisable] = useState(false);

  async function onSubmit(data) {
    try {
      const formData = new FormData();
      formData.append("data", JSON.stringify(data));
      formData.append("GSTUpload", data.GSTUpload[0]);
      formData.append("IATAUpload", data.IATAUpload[0]);
      formData.append("UploadAadhaar", data.UploadAadhaar[0]);
      formData.append("UploadPAN", data.UploadPAN[0]);
      formData.append("UploadPANPerson", data.UploadPANPerson[0]);
      formData.append("UploadUDYAM", data.UploadUDYAM[0]);

      setBtnDisable(true);
      const token = getCookieValue(tokenName);
      const endpoint = `${ipAddress}admin/distributors`;
      const res = await axios.post(endpoint, formData, {
        headers: {'Authorization': `Bearer ${token}`,}
      });

      // console.log(res.data);

      if (res.status === 200 && res.data.error) {
        errorToast(res.data.message);
      } else if (res.status === 200 && !res.data.error) {
        reset();
        successToast(res.data.message);
      }
      setBtnDisable(false);
    } catch (e) {
      setBtnDisable(false);
      errorToast(e.message);
    }
  }

  return (
    <>
      <CCard>
        <CCardHeader className="card-header-bg">
          <h4 className="mb-0 primary-color">Create New Distributor</h4>
        </CCardHeader>
        <CCardBody>
          <form onSubmit={handleSubmit(onSubmit)}>
            <div className="row">
              <div className="col-12 mb-3">
                <h5 className="text-muted">Member Information</h5>
              </div>

              {/* Business/Agency Name */}
              <div className="col-md-6 mb-3">
                <label htmlFor="AgencyName">
                  Business/Agency Name <span className="text-danger">*</span>
                </label>
                <input type="text" id="AgencyName" name="AgencyName" className="form-control"
                       {...register('AgencyName', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'AgencyName cannot contain "<" or ">"'
                       })} placeholder="Enter Business/Agency Name"
                />

                {errors?.AgencyName && <p className='text-danger'>{errors?.AgencyName?.message}</p>}
              </div>

              {/* Nature of Business*/}
              <div className="col-md-6 mb-3">
                <label htmlFor="NatureOfBusiness">
                  Nature of Business <span className="text-danger">*</span>
                </label>
                <select name="NatureOfBusiness" id="NatureOfBusiness" className="form-select"
                        {...register('NatureOfBusiness', {
                          required: "This is a required field.",
                        })}
                >
                  <option value="">Please Select Nature of Business</option>
                  <option value="Proprietorship">Proprietorship</option>
                  <option value="Individual">Individual</option>
                  <option value="Partnership Firm">Partnership Firm</option>
                  <option value="Private Limited">Private Limited</option>
                  <option value="Others">Others</option>
                </select>

                {errors?.NatureOfBusiness && <p className='text-danger'>{errors?.NatureOfBusiness?.message}</p>}
              </div>

              {/* Authorized Person Firstname */}
              <div className="col-md-6 mb-3">
                <label htmlFor="Firstname">
                  Authorized Person Firstname <span className="text-danger">*</span>
                </label>
                <input type="text" id="Firstname" name="Firstname" className="form-control"
                       {...register('Firstname', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'Firstname cannot contain "<" or ">"'
                       })} placeholder="Enter Firstname"
                />

                {errors?.Firstname && <p className='text-danger'>{errors?.Firstname?.message}</p>}
              </div>

              {/* Authorized Person Lastname */}
              <div className="col-md-6 mb-3">
                <label htmlFor="Lastname">Authorized Person Lastname <span className="text-danger">*</span></label>
                <input type="text" id="Lastname" name="Lastname" className="form-control"
                       {...register('Lastname', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'Lastname cannot contain "<" or ">"'
                       })} placeholder="Enter Lastname"
                />

                {errors?.Lastname && <p className='text-danger'>{errors?.Lastname?.message}</p>}
              </div>

              {/* Email */}
              <div className="col-md-6 mb-3">
                <label htmlFor="Email">Email <span className="text-danger">*</span></label>
                <input type="email" id="Email" name="Email" className="form-control"
                       {...register('Email', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'Email cannot contain "<" or ">"',
                         pattern: {
                           value: /^[^\s@]+@[^\s@]+\.[^\s@]+$/,
                           message: "Invalid email address"
                         }
                       })} placeholder="Enter Email"
                />

                {/*{errors?.Email && <p className='text-danger'>{errors?.Email?.message}</p>}*/}
                <ErrorMessage errors={errors} name="Email"
                              render={({message}) => <p className='text-danger'>{message}</p>}
                />
              </div>

              {/* Mobile Number */}
              <div className="col-md-6 mb-3">
                <label htmlFor="Mobile">Mobile Number <span className="text-danger">*</span></label>
                {/*<input type="text" id="Mobile" name="Mobile" className="form-control"*/}
                {/*       {...register('Mobile', {*/}
                {/*         required: "This is a required field.",*/}
                {/*         validate: value => !/<|>/.test(value) || 'Mobile cannot contain "<" or ">"'*/}
                {/*       })} placeholder="Enter Mobile Number"*/}
                {/*/>*/}

                <input type="text" id="Mobile" name="Mobile" className="form-control"
                       {...register('Mobile', {
                         required: "This is a required field.",
                         validate: {
                           noSpecialChars: value => !/[<>]/.test(value) || 'Mobile number cannot contain "<" or ">"',
                           onlyNumbers: value => /^\d+$/.test(value) || 'Mobile no. can only contain numbers',
                           exactLength: value => value.length === 10 || 'Mobile no. must be exactly 10 digits'
                           // maxLength: value => value.length <= 10 || 'Mobile no. cannot exceed 10 digits',
                         }
                       })} placeholder="Enter Mobile Number"
                />

                {errors?.Mobile && <p className='text-danger'>{errors?.Mobile?.message}</p>}
              </div>

              <div className="col-12 mt-2 mb-3">
                <h5 className="text-muted">Office Address</h5>
              </div>

              <div className="col-md-12 mb-3">
                <label htmlFor="Address">Address <span className="text-danger">*</span></label>
                <textarea name="Address" id="Address" className="form-control"
                          {...register('Address', {
                            required: "This is a required field.",
                            validate: value => !/<|>/.test(value) || 'Address cannot contain "<" or ">"'
                          })} placeholder="Enter Address"
                />

                {errors?.Address && <p className='text-danger'>{errors?.Address?.message}</p>}
              </div>

              <div className="col-md-6 mb-3">
                <label htmlFor="City">City <span className="text-danger">*</span></label>
                <input type="text" id="City" name="City" className="form-control"
                       {...register('City', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'City cannot contain "<" or ">"'
                       })} placeholder="Enter City"
                />

                {errors?.City && <p className='text-danger'>{errors?.City?.message}</p>}
              </div>

              <div className="col-md-6 mb-3">
                <label htmlFor="State">State <span className="text-danger">*</span></label>
                <input type="text" id="State" name="State" className="form-control"
                       {...register('State', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'State cannot contain "<" or ">"'
                       })} placeholder="Enter State"
                />

                {errors?.State && <p className='text-danger'>{errors?.State?.message}</p>}
              </div>

              <div className="col-12 mt-2 mb-3">
                <h5 className="text-muted">Authorized Person Documents</h5>
              </div>

              <div className="col-md-6 mb-3">
                <label htmlFor="AadhaarNumber">Aadhaar Number <span className="text-danger">*</span></label>
                <input type="text" id="AadhaarNumber" name="AadhaarNumber" className="form-control"
                       {...register('AadhaarNumber', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'AadhaarNumber cannot contain "<" or ">"'
                       })} placeholder="Enter AadhaarNumber"
                />

                {errors?.AadhaarNumber && <p className='text-danger'>{errors?.AadhaarNumber?.message}</p>}
              </div>

              <div className="col-md-6 mb-3">
                <label htmlFor="UploadAadhaar">Upload Aadhaar <span className="text-danger">*</span></label>
                <input type="file" id="UploadAadhaar" name="UploadAadhaar" className="form-control"
                       {...register('UploadAadhaar', {
                         required: "This is a required field."
                       })} accept=".png, .jpg, .jpeg, .pdf"
                />

                {errors?.UploadAadhaar && <p className='text-danger'>{errors?.UploadAadhaar?.message}</p>}
              </div>

              {/* PAN Number Person */}
              <div className="col-md-6 mb-3">
                <label htmlFor="PANNumberPerson">PAN Number <span className="text-danger">*</span></label>
                <input type="text" id="PANNumberPerson" name="PANNumberPerson" className="form-control"
                       {...register('PANNumberPerson', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'PAN Number cannot contain "<" or ">"'
                       })} placeholder="Enter PAN Number"
                />

                {errors?.PANNumberPerson && <p className='text-danger'>{errors?.PANNumberPerson?.message}</p>}
              </div>

              {/* Upload PAN Person */}
              <div className="col-md-6 mb-3">
                <label htmlFor="UploadPANPerson">Upload PAN <span className="text-danger">*</span></label>
                <input type="file" id="UploadPANPerson" name="UploadPANPerson" className="form-control"
                       {...register('UploadPANPerson', {
                         required: "This is a required field.",
                       })} accept=".png, .jpg, .jpeg, .pdf"
                />

                {errors?.UploadPANPerson && <p className='text-danger'>{errors?.UploadPANPerson?.message}</p>}
              </div>

              <div className="col-12 mt-2 mb-3">
                <h5 className="text-muted">Agency Documents</h5>
              </div>

              {/* GST Number */}
              <div className="col-md-6 mb-3">
                <label htmlFor="GSTNumber">GST Number <span className="text-danger">*</span></label>
                <input type="text" id="GSTNumber" name="GSTNumber" className="form-control"
                       {...register('GSTNumber', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'GSTNumber cannot contain "<" or ">"'
                       })} placeholder="Enter GST Number"
                />

                {errors?.AadhaarNumber && <p className='text-danger'>{errors?.AadhaarNumber?.message}</p>}
              </div>

              {/* Upload GST Certificate */}
              <div className="col-md-6 mb-3">
                <label htmlFor="GSTUpload">Upload GST Certificate <span className="text-danger">*</span></label>
                <input type="file" id="GSTUpload" name="GSTUpload" className="form-control"
                       {...register('GSTUpload', {
                         required: "This is a required field."
                       })} accept=".png, .jpg, .jpeg, .pdf"
                />

                {errors?.GSTUpload && <p className='text-danger'>{errors?.GSTUpload?.message}</p>}
              </div>

              {/* PAN Number Agency */}
              <div className="col-md-6 mb-3">
                <label htmlFor="PANNumber">PAN Number <span className="text-danger">*</span></label>
                <input type="text" id="PANNumber" name="PANNumber" className="form-control"
                       {...register('PANNumber', {
                         required: "This is a required field.",
                         validate: value => !/<|>/.test(value) || 'PAN Number cannot contain "<" or ">"'
                       })} placeholder="Enter PAN Number"
                />

                {errors?.PANNumber && <p className='text-danger'>{errors?.PANNumber?.message}</p>}
              </div>

              {/* Upload PAN Agency */}
              <div className="col-md-6 mb-3">
                <label htmlFor="UploadPAN">Upload PAN <span className="text-danger">*</span></label>
                <input type="file" id="UploadPAN" name="UploadPAN" className="form-control"
                       {...register('UploadPAN', {
                         required: "This is a required field.",
                       })} accept=".png, .jpg, .jpeg, .pdf"
                />

                {errors?.UploadPAN && <p className='text-danger'>{errors?.UploadPAN?.message}</p>}
              </div>

              {/* IATA Code */}
              <div className="col-md-6 mb-3">
                <label htmlFor="IATACode">IATA Code</label>
                <input type="text" id="IATACode" name="IATACode" className="form-control"
                       {...register('IATACode', {
                         validate: value => !/<|>/.test(value) || 'IATA Code cannot contain "<" or ">"'
                       })} placeholder="Enter IATA Code"
                />

                {/*{errors?.IATACode && <p className='text-danger'>{errors?.IATACode?.message}</p>}*/}
              </div>

              {/* Upload IATA */}
              <div className="col-md-6 mb-3">
                <label htmlFor="IATAUpload">Upload IATA</label>
                <input type="file" id="IATAUpload" name="IATAUpload" className="form-control"
                       {...register('IATAUpload')} accept=".png, .jpg, .jpeg, .pdf"
                />

                {/*{errors?.IATAUpload && <p className='text-danger'>{errors?.IATAUpload?.message}</p>}*/}
              </div>

              {/* UDYAM Number */}
              <div className="col-md-6 mb-3">
                <label htmlFor="UDYAM_Number">UDYAM Number</label>
                <input type="text" id="UDYAM_Number" name="UDYAM_Number" className="form-control"
                       {...register('UDYAM_Number', {
                         validate: value => !/<|>/.test(value) || 'UDYAM Number cannot contain "<" or ">"'
                       })} placeholder="Enter UDYAM Number"
                />

                {/*{errors?.UDYAM_Number && <p className='text-danger'>{errors?.UDYAM_Number?.message}</p>}*/}
              </div>

              {/* Upload UDYAM */}
              <div className="col-md-6 mb-3">
                <label htmlFor="UploadUDYAM">Upload UDYAM</label>
                <input type="file" id="UploadUDYAM" name="UploadUDYAM" className="form-control"
                       {...register('UploadUDYAM')} accept=".png, .jpg, .jpeg, .pdf"
                />

                {/*{errors?.UploadUDYAM && <p className='text-danger'>{errors?.UploadUDYAM?.message}</p>}*/}
              </div>

              <div className="col-12">
                <button disabled={btnDisable} className="px-5 btn btn-primary">
                  Create
                </button>
              </div>
            </div>
          </form>
        </CCardBody>
      </CCard>
    </>
  )
}
export default CreateDistributor;
